//Миксин - возможность делать шаблоны кода и переиспользовать его. Возможно добавлять аргументы.
@mixin style-link($color) {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    color: $color; }

@mixin clearfix {
    &::after {
        content: "";
        display: table;
        clear: both; } }



